	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Kino","date":"23 Nov 2003","slug":"/2003-11-23-kino/","tags":"Y2003"},"html":"<p><img alt=\"crh_nemo.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2003/11/crh_nemo.jpg\" width=\"240\" height=\"180\" border=\"0\" /></p>\n<p>Today we saw <a href=\"http://disney.go.com/disneyvideos/animatedfilms/findingnemo/index2.html\">Finding Nemo</a> (which just opened in Germany) at our local theater. It lived up to our high expectations.</p>\n<p>And yes, that's a bandage on Christopher's cheek. He had a high-speed encounter with a table corner.</p>"},"next":{"fields":{"title":"Donkeys and Guerillas","date":"22 Nov 2003","slug":"/2003-11-22-donkeys-and-guerillas/"}},"previous":{"fields":{"title":"(Not) Changing Religions","date":"23 Nov 2003","slug":"/2003-11-23-not-changing-religions/"}}},{"node":{"fields":{"title":"Donkeys and Guerillas","date":"22 Nov 2003","slug":"/2003-11-22-donkeys-and-guerillas/","tags":"Y2003"},"html":"<p><a title=\"Beasts of burden and terror in Iraq\" href=\"http://riverbendblog.blogspot.com/2003_11_01_riverbendblog_archive.html#106945179671764470\">Donkeys and Guerillas</a></p>"},"next":{"fields":{"title":"Plastic Pallet","date":"20 Nov 2003","slug":"/2003-11-20-plastic-pallet/"}},"previous":{"fields":{"title":"Kino","date":"23 Nov 2003","slug":"/2003-11-23-kino/"}}},{"node":{"fields":{"title":"Plastic Pallet","date":"20 Nov 2003","slug":"/2003-11-20-plastic-pallet/","tags":"Y2003"},"html":"<p>On the autobahn this morning, a plastic pallet fell from a truck onto the road. Hardly a notable event, except that it occurred directly in front of me. I was unable to avoid the obstacle, and thus drove over it. My right front tire did not survive the encounter, and neither did the wheel. Just to make things interesting, my spare was flat as well. </p>\n<p>All's well that ends well. The auto club pumped up the spare, I limped into my local <a href=\"http://www.atu.de\">ATU</a>, left them my car and took the bus to work. I now have 4 spanking new tires. And dirty fingernails. And am short 2 hours on my time sheet at work.</p>"},"next":{"fields":{"title":"Neo-Nazi Comment Spam","date":"19 Nov 2003","slug":"/2003-11-19-neo-nazi-comment-spam/"}},"previous":{"fields":{"title":"Donkeys and Guerillas","date":"22 Nov 2003","slug":"/2003-11-22-donkeys-and-guerillas/"}}}],"pathPrefix":"","first":false,"last":false,"index":585,"pageCount":1101}}

